import * as THREE from 'three'
import loadTexture from '../../../lib3d/loader/textureLoader'
import anime from 'animejs'

import starLightPng from '../assets/starLight.png'
import starPng from '../assets/star.png'

class Glimmer extends THREE.Object3D {
  constructor (showStar = false, scale = 0.8) {
    super()

    Promise.all([loadTexture(starLightPng), loadTexture(starPng)]).then(([starlight, star]) => {
      var material = new THREE.MeshBasicMaterial({opacity: 0.8, map: starlight, transparent: true})

      var geometry = new THREE.PlaneGeometry(6, 6, 1, 1)
      this.plane = new THREE.Mesh(geometry, material)
      this.add(this.plane)

      var materialSub = new THREE.MeshBasicMaterial({opacity: 0.4, map: starlight, transparent: true})
      var geometrySub = new THREE.PlaneGeometry(4, 4, 1, 1)
      this.planeSub = new THREE.Mesh(geometrySub, materialSub)
      this.add(this.planeSub)
      this.planeSub.position.z = 0.1

      if (showStar) {
        var geometryStar = new THREE.PlaneGeometry(2, 2, 1, 1)
        var materialStar = new THREE.MeshBasicMaterial({map: star, transparent: true})
        var starPlane = new THREE.Mesh(geometryStar, materialStar)
        this.add(starPlane)
        starPlane.position.z = 0.2
      }

      this.scale.copy(new THREE.Vector3(scale, scale, 1))

      this.animate()
    })
  }

    animate = () => {
      var coord = this.coord = {x: 0.2}
      let thred = 0.5
      let [a, b] = getAb(0.2, 0.2, thred, 1)
      let [a1, b1] = getAb(thred, 1, 1, 0)

      anime({
        targets: coord,
        x: 1,
        easing: 'linear',
        duration: 1200,
        loop: true,
        update: () => {
          this.plane.scale.x = coord.x
          this.plane.scale.y = coord.x
          if (coord.x < thred) {
            this.plane.material.opacity = a * coord.x + b
          } else {
            this.plane.material.opacity = a1 * coord.x + b1
          }
        }
      })

      var coord2 = this.coord2 = {x: 0.2}
      let thred2 = 0.7
      let [a2, b2] = getAb(0.2, 0.2, thred2, 1)
      let [a3, b3] = getAb(thred2, 1, 1, 0)

      anime({
        targets: coord2,
        x: 1,
        easing: 'linear',
        duration: 1200,
        loop: true,
        update: () => {
          this.planeSub.scale.x = coord2.x
          this.planeSub.scale.y = coord2.x
          if (coord.x < thred) {
            this.planeSub.material.opacity = a2 * coord2.x + b2
          } else {
            this.planeSub.material.opacity = a3 * coord2.x + b3
          }
        }
      })
    }

  dispose = () => {
    anime.remove(this.coord)
    anime.remove(this.coord2)
  }
}

// y = ax + bab
function getAb (x1, y1, x2, y2) {
  return [(y1 - y2) / (x1 - x2), (x2 * y1 - x1 * y2) / (x2 - x1)]
}

export default Glimmer
