import * as THREE from 'three'
import loadTexture from '../../../lib3d/loader/textureLoader'

import anime from 'animejs'

import flashPng from '../assets/flash.png'

const meshWidth = 0.20
const meshHeight = 64 * meshWidth
const meshSegment = 4

const lightingMaterial = new THREE.MeshBasicMaterial({
  depthWrite: false,
  transparent: true
})

const tubeMaterial = new THREE.MeshBasicMaterial({
  color: 0xf8b62d,
  opacity: 0.3,
  depthWrite: false,
  transparent: true
})
// tubeMaterial.wireframe = true

class Flashing extends THREE.Object3D {
  constructor (v1, v2, direction = 1) {
    super()
    this.v1 = v1
    this.v2 = v2
    this.index = 0
    this.curves = []

    const dir = new THREE.Vector3() // create once an reuse it

    dir.subVectors(v2, v1).normalize()

    this.cross = new THREE.Vector3()
    this.cross.crossVectors(dir, new THREE.Vector3(0, 0, 1)).normalize()

    loadTexture(flashPng).then((map) => {
      lightingMaterial.map = map

      this.curves.push(this.drawLine(9 * direction))
      this.curves.push(this.drawLine(12 * direction))

      const loop = () => {
        this.timerKey = setTimeout(() => {
          !document.hidden && this.emit()
          loop()
        }, Math.random() * 2000 + 600)
      }

      loop()
    })
  }

  drawLine = (offset) => {
    let curve = new THREE.QuadraticBezierCurve3(
      this.v1,
      new THREE.Vector3((this.v1.x + this.v2.x) * 0.5, (this.v1.y + this.v2.y) * 0.5, 10).addScaledVector(this.cross, offset),
      this.v2
    )

    let tubeGeometry = new THREE.TubeGeometry(curve, 12, 0.2, 4)

    let tubeMesh = new THREE.Mesh(tubeGeometry, tubeMaterial)

    this.add(tubeMesh)

    return {
      curve,
      curveLength: curve.getLength()
    }
  }

  emit = () => {
    this.index++

    const {curve, curveLength} = this.curves[this.index % this.curves.length]

    let planemesh = getPlane(lightingMaterial)

    const ratio = meshHeight / curveLength
    let coord = {x: 0}
    let delta = ratio / meshSegment
    const zBias = 0.01

    anime({
      targets: coord,
      x: 1 + ratio,
      easing: 'linear',
      duration: curveLength / 0.02,
      begin: () => {
        this.add(planemesh)
      },
      update: () => {
        let position = planemesh.geometry.attributes.position
        let index = 0
        let array = position.array

        let cur = coord.x + delta
        for (let i = 0; i < position.count / 2; i++) {
          cur = cur - delta

          const curveP = curve.getPoint(cur < 0 ? 0 : cur)

          array[index++] = curveP.x - this.cross.x * meshWidth
          array[index++] = curveP.y - this.cross.y * meshWidth
          array[index++] = curveP.z + zBias

          array[index++] = curveP.x + this.cross.x * meshWidth
          array[index++] = curveP.y + this.cross.y * meshWidth
          array[index++] = curveP.z + zBias
        }
        planemesh.geometry.attributes.position.needsUpdate = true
      },
      complete: () => {
        this.remove(planemesh)
        recyclePlane(planemesh)
      }
    })
  }

  dispose = () => {
    clearTimeout(this.timerKey)
  }
}


let cache = []
function getPlane () {
  if (cache.length === 0) {
    let planeBufferGeometry = new THREE.PlaneBufferGeometry(meshWidth, meshHeight, 1, meshSegment)
    return new THREE.Mesh(planeBufferGeometry, lightingMaterial)
  } else {
    let plane = cache.pop()
    return plane
  }
}

function recyclePlane (obj) {
  cache.push(obj)
}

export default Flashing
