import * as THREE from 'three'
import loadTexture from '../../../lib3d/loader/textureLoader'

import chinaMapx2 from '../assets/china_mapx2.png'

class Map extends THREE.Object3D {
  constructor () {
    super()

    loadTexture(chinaMapx2).then((map) => {
      const geometry = new THREE.PlaneGeometry(100, 100, 1, 1)
      const material = new THREE.MeshBasicMaterial({map, transparent: true})
      const plane = new THREE.Mesh(geometry, material)
      this.add(plane)
    })
  }

  dispose = () => {

  }
}

export default Map
