import * as THREE from 'three'
import Map from './instance/map'
import Flashing from './instance/flashing'
import FlashingSmall from './instance/flashingSmall'
import Glimmer from './instance/glimmer'
import CameraController from '../../lib3d/cameraController'
import { clearThree } from '../../lib3d/utils'

class ThreeJsMap {
  constructor (htmlRoot, width = 800, height = 800) {
    this.clock = new THREE.Clock()

    this.sceneMap = new THREE.Scene()
    this.scene = new THREE.Scene()
    this.sceneMap.add(new Map())

    this.cameraController = new CameraController(width, height, htmlRoot)

    // scene.add(new THREE.AxesHelper(10));

    // fujian
    var fujianVec3 = new THREE.Vector3(32, -20, 0)

    // sichuang
    var sichuangVec3 = new THREE.Vector3(0, -12, 0)

    var guangdongVec3 = new THREE.Vector3(22, -27, 0)

    // shanxi
    var shanxiVec3 = new THREE.Vector3(11, -5, 0)

    // hubei
    var yunnanVec3 = new THREE.Vector3(-1, -27, 0)

    // beijing
    var beijingVec3 = new THREE.Vector3(24, 10.5, 0)

    // sichuang => beijing
    this.scene.add(new Flashing(fujianVec3, beijingVec3))

    // fujian => beijing
    this.scene.add(new Flashing(sichuangVec3, beijingVec3, -1.4))

    // guangdong => beijing
    this.scene.add(new Flashing(guangdongVec3, beijingVec3))

    // shanxi => beijing
    this.scene.add(new Flashing(shanxiVec3, beijingVec3, -0.6))

    // yunnan => beijing
    this.scene.add(new Flashing(yunnanVec3, beijingVec3, 0.7))

    let glimmer = new Glimmer()
    this.scene.add(glimmer)

    let glimmer2 = new Glimmer()
    this.scene.add(glimmer2)

    let glimmer3 = new Glimmer(true, 1)
    this.scene.add(glimmer3)

    let glimmer4 = new Glimmer()
    this.scene.add(glimmer4)

    let glimmer5 = new Glimmer()
    this.scene.add(glimmer5)

    let glimmer6 = new Glimmer()
    this.scene.add(glimmer6)

    glimmer.position.set(fujianVec3.x, fujianVec3.y, fujianVec3.z + 2)
    glimmer2.position.set(sichuangVec3.x, sichuangVec3.y, sichuangVec3.z + 2)
    glimmer3.position.set(beijingVec3.x, beijingVec3.y, beijingVec3.z + 2)
    glimmer4.position.set(guangdongVec3.x, guangdongVec3.y, guangdongVec3.z + 2)
    glimmer5.position.set(shanxiVec3.x, shanxiVec3.y, shanxiVec3.z + 2)
    glimmer6.position.set(yunnanVec3.x, yunnanVec3.y, yunnanVec3.z + 2)

    const mat1 = new THREE.MeshBasicMaterial({
      color: 0xffffff
    })
    const axis = new THREE.Vector3(0, 0, 1)

    let mergeGeometry = new THREE.Geometry()

    let fakeData = [
      { angle: 30,
        distance: 4 },
      { angle: 90,
        distance: 5 },
      { angle: 120,
        distance: 5.6 },
      { angle: 160,
        distance: 8 },
      { angle: 180,
        distance: 7 },
      { angle: 220,
        distance: 7 },
      { angle: 240,
        distance: 7.8 },
      { angle: 260,
        distance: 6 },
      { angle: 290,
        distance: 5 },
      { angle: 310,
        distance: 7 },
      { angle: 340,
        distance: 5 },
      { angle: 358,
        distance: 8 }
    ]
    // sichuang
    for (let i = 0; i < fakeData.length; i++) {
      let angle = fakeData[i].angle / 360 * Math.PI * 2
      let vector = new THREE.Vector3(1, 0, 0)
      let dir = vector.applyAxisAngle(axis, angle)
      let pos = sichuangVec3.clone().addScaledVector(dir, fakeData[i].distance)

      let crcleGeometry = new THREE.CircleGeometry(0.32)
      let matrix = new THREE.Matrix4()
      matrix.setPosition(new THREE.Vector3().copy(pos).setZ(2))
      mergeGeometry.merge(crcleGeometry, matrix)

      this.scene.add(new FlashingSmall(pos, sichuangVec3))
    }

    // guangdong
    fakeData = [
      { angle: 30,
        distance: 3 },
      { angle: 90,
        distance: 2 },
      { angle: 180,
        distance: 3 },
      { angle: 220,
        distance: 6 },
      { angle: 260,
        distance: 4 },
      { angle: 300,
        distance: 2 },
      { angle: 340,
        distance: 5 },
      { angle: 358,
        distance: 6 }
    ]

    for (let i = 0; i < fakeData.length; i++) {
      let angle = fakeData[i].angle / 360 * Math.PI * 2
      let vector = new THREE.Vector3(1, 0, 0)
      let dir = vector.applyAxisAngle(axis, angle)
      let pos = guangdongVec3.clone().addScaledVector(dir, fakeData[i].distance)

      let crcleGeometry = new THREE.CircleGeometry(0.32)
      let matrix = new THREE.Matrix4()
      matrix.setPosition(new THREE.Vector3().copy(pos).setZ(2))
      mergeGeometry.merge(crcleGeometry, matrix)

      this.scene.add(new FlashingSmall(pos, guangdongVec3))
    }

    // fujian
    fakeData = [
      { angle: 30,
        distance: 2 },
      { angle: 120,
        distance: 2 },
      { angle: 180,
        distance: 3 },
      { angle: 220,
        distance: 6 },
      { angle: 240,
        distance: 6 },
      { angle: 260,
        distance: 4 },
      { angle: 300,
        distance: 2 }
    ]

    for (let i = 0; i < fakeData.length; i++) {
      let angle = fakeData[i].angle / 360 * Math.PI * 2
      let vector = new THREE.Vector3(1, 0, 0)
      let dir = vector.applyAxisAngle(axis, angle)
      let pos = fujianVec3.clone().addScaledVector(dir, fakeData[i].distance)

      let crcleGeometry = new THREE.CircleGeometry(0.32)
      let matrix = new THREE.Matrix4()
      matrix.setPosition(new THREE.Vector3().copy(pos).setZ(2))
      mergeGeometry.merge(crcleGeometry, matrix)

      this.scene.add(new FlashingSmall(pos, fujianVec3))
    }

    // shanxi
    fakeData = [
      { angle: 30,
        distance: 2 },
      { angle: 90,
        distance: 8 },
      { angle: 80,
        distance: 10 },
      { angle: 120,
        distance: 3 },
      { angle: 180,
        distance: 4 },
      { angle: 220,
        distance: 2.8 },
      { angle: 260,
        distance: 2.6 },
      { angle: 300,
        distance: 2 }
    ]

    for (let i = 0; i < fakeData.length; i++) {
      let angle = fakeData[i].angle / 360 * Math.PI * 2
      let vector = new THREE.Vector3(1, 0, 0)
      let dir = vector.applyAxisAngle(axis, angle)
      let pos = shanxiVec3.clone().addScaledVector(dir, fakeData[i].distance)

      let crcleGeometry = new THREE.CircleGeometry(0.32)
      let matrix = new THREE.Matrix4()
      matrix.setPosition(new THREE.Vector3().copy(pos).setZ(2))
      mergeGeometry.merge(crcleGeometry, matrix)

      this.scene.add(new FlashingSmall(pos, shanxiVec3))
    }

    // yunnan
    fakeData = [
      {
        angle: 0,
        distance: 3.4
      },
      { angle: 30,
        distance: 3.4 },
      { angle: 60,
        distance: 5.1 },
      { angle: 90,
        distance: 3.6 },
      { angle: 128,
        distance: 8.6 },
      { angle: 150,
        distance: 7.2 },
      { angle: 180,
        distance: 6 },
      { angle: 200,
        distance: 6.8 },
      { angle: 220,
        distance: 6.8 },
      { angle: 260,
        distance: 2.6 },
      { angle: 300,
        distance: 2 }
    ]

    for (let i = 0; i < fakeData.length; i++) {
      let angle = fakeData[i].angle / 360 * Math.PI * 2
      let vector = new THREE.Vector3(1, 0, 0)
      let dir = vector.applyAxisAngle(axis, angle)
      let pos = yunnanVec3.clone().addScaledVector(dir, fakeData[i].distance)

      let crcleGeometry = new THREE.CircleGeometry(0.32)
      let matrix = new THREE.Matrix4()
      matrix.setPosition(new THREE.Vector3().copy(pos).setZ(2))
      mergeGeometry.merge(crcleGeometry, matrix)

      this.scene.add(new FlashingSmall(pos, yunnanVec3))
    }

    this.scene.add(new THREE.Mesh(mergeGeometry, mat1))

    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true, precision: 'highp' })
    this.renderer.setPixelRatio(Math.max(window.devicePixelRatio, 2))
    this.renderer.setSize(width, height)
    htmlRoot.appendChild(this.renderer.domElement)

    const animate = () => {
      const delta = this.clock.getDelta()

      const camera = this.cameraController.getCamera()

      this.cameraController.updateCamera(delta)

      this.renderer.autoClear = true
      this.renderer.render(this.sceneMap, camera)

      this.renderer.autoClear = false
      this.renderer.render(this.scene, camera)

      this.timerKey = window.requestAnimationFrame(animate)
    }

    animate()
  }

  resize = (width, height) => {
    this.cameraController.resizeCamera(width, height)
    this.renderer.setSize(width, height)
  }

  dispose = () => {
    clearThree(this.sceneMap)
    clearThree(this.scene)
    this.renderer.dispose()
    this.cameraController.dispose()
    window.cancelAnimationFrame(this.timerKey)
  }
}

export default ThreeJsMap
