import React from 'react'
import ReactDOM from 'react-dom'
import ThreeJsMap from './ThreeJsMap'

export default class ChinaMap extends React.Component {
  componentDidMount () {
    let root = ReactDOM.findDOMNode(this)
    this.three = new ThreeJsMap(root, root.offsetWidth, root.offsetHeight)
  }

  componentWillUnmount () {
    this.three.dispose()
    this.three = null
  }

  render () {
    const {props} = this
    return <div style={props.style} className={props.className} />
  }
}
